
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState } from 'vuex'
export default {
name: 'topiclist',
props: ['name'],
components: {
Cell,
Group,
Scroller,
Tab,
TabItem
},
computed: {
...mapState(['topics'])

},
data() {
return {
lists: [],

page: 1,

limit: 15,

pullupConfig: {
content: '~~~',
downContent:'~~~',
upContent:'',
loadingContent: '...'
},

pulldownConfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:'...'
}
};
},
methods: {

freshData:function(){
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(response){
if(response.data&&response.data.success){
app.lists=response.data.data;
app.page=1;

app.$refs.pullup.donePulldown();
}
})
},

getDescByTab(tab) {
return this.$store.getters.getTopicDescByTab(tab)
},

routeto(index) {




var url = `/topiclist/${this.topics[index].name}`;
this.$router.push(url);
},

loadMoreData() {
this.page ++;
var app = this;
this.$http.get('/topics',{
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
}).then(function(response) {
if(response.data.success) {
app.lists = app.lists.concat(response.data.data);


app.$refs.pullup.donePullup();
}
})
}
},


beforeRouteEnter(to,from,next) {





next(function(vm) {

vm.$http.get('/topics',{
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit
}
}).then(function(response) {
if(response.data.success) {
vm.lists = response.data.data;
}
});
});
},

beforeRouteUpdate(to,from,next) {
var app = this;

this.$http.get('/topics',{
params: {
tab: to.params.name,
page: 1,
limit: this.limit
}
}).then(function(response) {
if(response.data.success) {

app.lists = response.data.data;
}
})

next();
}
}



