import { mapState } from 'vuex'
export default {
    name: 'center',
    computed: {
        ...mapState(['loginname'])
    },
    created() {
        this.$router.replace('/user/' + this.loginname)
        // POST /5c7cc54d5b8cb21491ca59da/reply HTTP/1.1

    }
}



