
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState, mapGetters, mapMutations } from "vuex";
import VmBackTop from "vue-multiple-back-top";
export default {
name: "app",
data:function(){
return{
badgeText:''
}
},
components: {
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet,
VmBackTop,
Badge
},
computed: {
...mapState(["loginedMoreMenus", "unLoginedMoreMenus", "isLogined"]),
...mapGetters(["moreMenus"]),
isShowMoreMenus: {

get: function() {
return this.$store.state.isShowMoreMenus;
},

set: function(nval) {

this.$store.commit("mutationIsShowMoreMneus", nval);
}
}
},
created() {

var loginStatus = window.localStorage.getItem("loginname");

if (loginStatus) {

this.mutationLogin();
this.mutationLoginname(window.localStorage.getItem("loginname"));
this.mutationAccessToken(window.localStorage.getItem("accesstoken"));
this.mutationAuthorId(window.localStorage.getItem("author_id"));
}
if(this.isLogined){

this.getUnreadMsgCount();

var app=this;
window.setInterval(function(){
this.getUnreadMsgCount();
},5*60*1000);
}
},
methods: {

getUnreadMsgCount:function(){
var app=this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(response){

if(response.data.success){
app.badgeText=response.data.data;
}
})
},
...mapMutations([
"mutationLogin",
"mutationLoginname",
"mutationAccessToken",
"mutationAuthorId"
]),
routetohome() {
this.$router.replace("/");
},

showMoreMenus() {

this.isShowMoreMenus = true;
},

handleClickMoreMenu(menukey, menuitem) {

this.$router.push("/" + menukey);
}
}
};



