
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapMutations } from 'vuex';
export default {
name: 'login',
components: {
Group,
XInput,
XButton,
Toast
},
data () {
return {
accesstoken: '',
isShowToast:false,
toastText:''
};
},
methods: {
...mapMutations(['mutationLogin', 'mutationLoginname', 'mutationAccessToken', 'mutationAuthorId']),

dologin () {
var app = this;
this.$http.post('/accesstoken', {
accesstoken: this.accesstoken
}).then((response) => {





if (response && response.data && response.data.success) {

this.mutationLogin();

window.localStorage.setItem('loginStatus', true);
window.localStorage.setItem('loginname', response.data.loginname);
window.localStorage.setItem('accesstoken', this.accesstoken);
window.localStorage.setItem('author_id', response.data.id)

this.mutationAuthorId(response.data.id);
this.mutationLoginname(response.data.loginname);

this.mutationAccessToken(this.accesstoken);

this.$router.push('/user/' + response.data.loginname);
}
}).catch(function(error){



app.isShowToast=true;
app.toastText=error.response.data.error_msg;
});
}
}
}



