// vuex
import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
// 
Vue.use(Vuex);

// Store
const store = new Vuex.Store({
  // 
  state: {
    // 
    topics: [{
        name: 'all',
        desc: ''
      },
      {
        name: 'good',
        desc: ''
      },
      {
        name: 'share',
        desc: ''
      },
      {
        name: 'ask',
        desc: ''
      },
      {
        name: 'job',
        desc: ''
      },
      {
        name: 'dev',
        desc: ''
      }
    ],
    // 
    isShowMoreMenus: false,
    // 
    loginname: '',
    // accesstoken
    accesstoken: '',
    // id
    author_id: '',
    isLogined: false,
    // 
    loginedMoreMenus: {
      create: '',
      center: '',
      msg: '',
      logout: ''
    },
    // 
    unLoginedMoreMenus: {
      login: '',
    }
  },
  // state
  getters: {
    moreMenus(state) {
      if (state.isLogined) {
        return state.loginedMoreMenus;
      }
      return state.unLoginedMoreMenus;
    },
    getTopicDescByTab(state) {
      //  
      return function (tab) {
        for (var i = 0; i < state.topics.length; i++) {
          if (state.topics[i].name == tab) {
            return state.topics[i].desc;
          }
        }
      }
    }
  },
  mutations,
  actions
});

export default store;



