
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'user',
props: ['loginname'],
components: {
Card,
Group,
Cell
},
data () {
return {
userinfo: null,
collect_topics: []
}
},
beforeRouteEnter (to, from, next) {
next(function (vm) {

vm.$http.get('/user/' + vm.loginname).then((response) => {
vm.userinfo = response.data.data;
})

vm.$http.get('/topic_collect/' + vm.loginname).then((response) => {
vm.collect_topics = response.data.data;
})
});
},
beforeRouteUpdate (to, from, next) {
this.$http.get('/user/' + this.loginname).then((response) => {
this.userinfo = response.data.data;
})
this.$http.get('/topic_collect/' + this.loginname).then((response) => {
this.collect_topics = response.data.data;
})
next();
}
}



