
import Msg from 'vux/src/components/msg/index.vue'

export default {
name: 'logout',
data(){
return{
isShowMsg:false,
buttons:[
{
type:'primary',
text:'',
link:'/login'
}
]
}
},
components:{
Msg
},
created() {

if(false==this.$store.state.isLogined){
this.isShowMsg=true;
console.log('')
}


this.$store.commit('mutationLogout');


var storage = window.localStorage;
storage.removeItem('loginStatus');
storage.removeItem('loginname');
storage.removeItem('accesstoken');

this.$router.push('/')
}

}



